.app {
  // background: linear-gradient(45deg, var(--primary-color), transparent);
  // background-size: cover;
  font-family: var(--font-base);
  // background-blend-mode: lighten;
}

body {
  background-image: url("./assets/bg2.jpg"), linear-gradient(45deg, rgb(15, 15, 19), rgb(18, 18, 24));
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.app__whitebg {
  // background-image: url("assets/bg2.jpg"), linear-gradient(45deg, rgb(34, 34, 48), rgb(39, 39, 58));
  // background-size: cover;
}

.app__primarybg {
  // background-image: url("assets/bg3.gif"), linear-gradient(45deg, rgb(15, 15, 19), rgb(18, 18, 24));
  // background-blend-mode: lighten;
}

.app__container {
  width: 100%;
  min-height: 100vh;

  display: flex;
  flex-direction: row;
}

.app__flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__wrapper {
  flex: 1;
  width: 100%;
  flex-direction: column;
  padding: 4rem 2rem;

  @media screen and (max-width: 450px) {
    padding: 4rem 1rem 2rem;
  }
}

.copyright {
  width: 100%;
  padding: 2rem 0 0;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;

  p {
    text-transform: uppercase;
  }
}

.head-text {
  font-size: 2.75rem;
  font-weight: 800;
  text-align: center;
  color: var(--head-textcolor);
  text-transform: capitalize;

  span {
    color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    font-size: 4rem;
  }

  @media screen and (max-width: 450px) {
    font-size: 2rem;
  }
}

.subhead-text{
  font-size: 1.20rem;
  font-weight: 500;
  text-align: center;
  color: var(--gray-color);
  text-transform: capitalize;
}

.hidden {
  display : none;
}

.p-text {
  font-size: 0.8rem;
  text-align: left;
  color: var(--gray-color);
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.p1-text {
  font-size: 0.8rem;
  text-align: left;
  color: #cdcaec;
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.bold-text {
  font-size: 1rem;
  font-weight: 800;
  color: white;
  text-align: left;

  @media screen and (min-width: 2000px) {
    font-size: 2rem;
  }

  @media screen and (max-width: 450px) {
    font-size: 0.9rem;
  }
}

.app__social {
  display: flex;
  position: fixed;
  bottom: 0;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  padding: 1rem;

  div {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0.25rem 0;
    background-color: #232756;
    box-shadow: 3px 3px 12px;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: all 0.3s ease-in-out;

    svg {
      width: 15px;
      height: 15px;
      color: var(--white-color);
    }

    &:hover {
      background-color: rgb(27, 10, 73);
      border-color: rgb(27, 10, 73);

      svg {
        color: var(--white-color);
      }
    }

    @media screen and (min-width: 2000px) {
      width: 70px;
      height: 70px;

      margin: 0.5rem 0;

      svg {
        width: 30px;
        height: 30px;
      }
    }
  }
}

.app__navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  padding: 1rem;

  .app__navigation-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #cbcbcb;
    margin: 0.5rem;

    transition: background-color 0.2s ease-in-out;

    &:hover {
      background-color: var(--secondary-color);
    }

    @media screen and (min-width: 2000px) {
      width: 20px;
      height: 20px;
    }
  }
}

@media screen and (max-width: 500px) {
  .app__navigation,
  .app__social {
    display: none;
  }

  .copyright {
    padding: 2rem;
  }
}